.graphData{
    padding: 15px;

    h4{
        margin: 0px;
    }
    h2{
        margin: 0px;
    }
}
.graphData-container{
    .itemOne{
        border: none;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px dotted var(--grayTextColor);
    }
}
.graphData-btn{
    opacity: 0;
    transform: translateX(50px);
}
.graphData:hover .graphData-btn{
opacity: 1;
transition: 0.5s ease-in-out;
transform: translateX(0px);
}